<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
transition(appear='' :enter-active-class='`animate-top-left-enter`' :leave-active-class='`animate-top-left-leave`' :duration='250')
  .c-first-time-here-corner.anim-short(v-if='shown')
    .c-first-time-here-corner__circle
      .c-first-time-here-corner__inside.anim-base.animate-fade-in-up
        h3
          span.anim-slow.animate-wobble-hand 👋
          | First time here ?
          close-icon(:size='4' @click='close()')
        p
          | Consider reading our 
          a(:href="guidePage") guide
          | ,
        p
          | or consulting our 
          a(:href='bestProductsPage') best products
        p Welcome !
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
import CloseIcon from '@/components/utils/CloseIcon'

export default {
  name: 'FirstTimeHereCorner',

  components: {
    CloseIcon
  },

  props: {
    guidePage: {
      type: String,
      required: false,
      default: '/guide'
    },
    bestProductsPage: {
      type: String,
      required: false,
      default: '/best_products'
    }
  },

  data: () => ({
    // --> STATE <--

    shown: false
  }),

  mounted() {
    if (!localStorage.getItem('growthBunkerFirstTimeHereCorner_visited')) {
      this.shown = true
    }
  },

  methods: {
    close() {
      this.closing = true

      setTimeout(() => {
        this.shown = false
        this.closing = false
        localStorage.setItem('growthBunkerFirstTimeHereCorner_visited', true)
      }, 250)
    }
  }
}
</script>

