export const externalComponent = url => {
  const name = url
    .split('/')
    .reverse()[0]
    .match(/^(.*?)\.umd/)[1]

  if (window[name]) return window[name]

  window[name] = new Promise((resolve, reject) => {
    const script = document.createElement('script')
    script.async = true
    script.addEventListener('load', () => {
      resolve(window[name])
    })
    script.addEventListener('error', () => {
      reject(new Error(`Error loading ${url}`))
    })
    script.src = url
    document.head.appendChild(script)
  })

  return window[name]
}

export const getFontSize = value => {
  switch (value) {
    case 1:
      return 'text-base'
    case 2:
      return 'text-lg'
    case 3:
      return 'text-xl'
    case 4:
      return 'text-2xl'
    case 5:
      return 'text-3xl'
    default:
      return value
  }
}

export default {
  install(Vue) {
    const helpers = {
      externalComponent,
      getFontSize
    }

    Vue.prototype.$helpers = helpers
    window.$helpers = helpers
  }
}
